import React, { Component } from 'react';
import './SignIn.css'
import 'bootstrap-social';



class SignIn extends Component {

  render() {
    return (
      <div className="SignIn">
        
        <label htmlFor="email"><b>Email:</b></label>
            <input type="email" 
                    id="txtEmail"  
                    className="form-control" 
                    required />
            <br />
            <label htmlFor="password">
                    <b>Password:</b>
            </label>
            <input type="password" 
                    id="txtPassword" 
                    className="form-control" 
                    required />
            <div className="clearfix">
                <button type="submit" id="btnLogIn" className="btn btn-primary">Log in</button>
                <button type="submit" id="btnLogOut" className="btn btn-light hide">Log Out</button>
                <button type="submit" id="btnSignUp" className="btn btn-secondary">Sign Up</button>
            </div>
            <div className="clearfix">
                <button type="submit" 
                        id="btnSignInGithub" 
                        className="btn btn-block btn-social btn-github">
                        <span className="fa fa-github"></span> 
                        Sign in with Github
                </button>
                <br />
                <button type="submit" 
                        id="btnSignInGoogle" 
                        className="btn btn-block btn-social btn-google">
                        <span className="fa fa-google"></span> 
                        Sign in with Google
                </button>
            </div>


      </div>
    );
  }
}

export default SignIn;



